import api from '@/api/member';
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
import tuiListCell from "@/components/list-cell/list-cell"
import tuiButton from "@/components/extend/button/button"
import tuiFooter from "@/components/footer/footer"
export default {
	components: {
		tuiListCell,
		tuiButton,
		tuiFooter
	},

	data() {
		return {
			SessionKey: '',
			OpenId: '',
			/* nickName: "哈尼",
			phoneNumber: null, */
			avatarUrl: null,
			mobile: "",
			isCanUse: uni.getStorageSync('isCanUse') || true, //默认为true
			today: null
		}
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'userName', 'session_key']),
	onLoad() {
		this.mobile = uni.getStorageSync('mobile');
		this.avatarUrl = uni.getStorageSync('avatarUrl');
		console.log(JSON.stringify(this.mobile))
		this.today = new Date().getFullYear()
	},
	onShow() {
		this.mobile = uni.getStorageSync('mobile');
		this.avatarUrl = uni.getStorageSync('avatarUrl');
		if (!this.hasLogin) {
			/**
			 * 如果需要强制登录，使用reLaunch方式
			 */
			if (this.forcedLogin) {
				setTimeout(() => {
					uni.reLaunch({
						url: '../login/login'
					});
				}, 200);
			} else {
				setTimeout(() => {
					uni.navigateTo({
						url: '../login/login'
					});
				}, 200);
			}
		} else {

		}

	},
	methods: {
		href(page) {
			let url = "";
			switch (page) {
				case 1:
					url = "../extend-view/userInfo/userInfo"
					break;
				case 2:
					url = "../extend-view/myOrder/myOrder"

					break;
				case 3:
					url = '../extend-view/shopcart/shopcart'
					break;
				case 4:
					url = "../extend-view/facorites/facorites"
					break;
				case 5:
					//url = '../extend-view/news/news'
					uni.requestSubscribeMessage({
						tmplIds: [''],
						success(res) {}
					})
					break;
				case 6:
					url = '../extend-view/address/address'
					break;
				default:
					break;
			}
			console.log(url)
			if (url) {
				uni.navigateTo({
					url: url
				})
			} else {
				this.tui.toast("功能尚未完善~")
			}
		},
	}
}
